<template>
  <div
    class="modal fade modal-lg"
    id="exampleModal"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">{{img_name}}</h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body "><img :src="img_url" class="w-100"/></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "model_popup",
  props:{
    img_name:String,
    img_url:String
  }
};
</script>

